<template>
  <section id="home" :style="{backgroundImage:'url('+bg+')'}">
    <!--用户信息-->
    <user></user>
     <!--游戏专区-->
     <div class="centerTxt">
       <router-link to="/games"><div class="textLeft"><img src="./../assets/images/game7.png" alt=""></div></router-link>
       <router-link to="/planningArea"><div class="textRight"><img src="./../assets/images/game6.png" alt=""></div></router-link>
     </div>
     <!--客服人物-->
     <div class="serverImg">
       <img src="./../assets/images/game8.png" alt="">
     </div>
     <!-- 底部导航菜单 -->
     <tabs :selected="selected"></tabs>
  </section>
</template>

<script>
import tabs from './../components/tabs'
import user from './../components/user'
export default {
  name: 'home',
  data () {
    return {
      selected: 'home', // 导航当前页的状态
      bg: require('./../assets/images/game1.png'), // 背景图片
      QR: require('../assets/images/game4.png') // 用户头像
    }
  },
  components: {
    tabs, user
  }
}
</script>

<style scoped >
#home{background-size: 100% 100%;height: 100%;position: fixed;width: 100%;}

/*内容样式  */
.centerTxt{clear: both; display: inline-block; width: 100%;}
.centerTxt .textLeft{float: left;padding-left: 1.5rem;}
.centerTxt .textLeft img{width: 9rem;}
.centerTxt .textRight{float: right;}
.centerTxt .textRight img{width: 9rem;padding-right: 1.5rem;}

/*客服头像样式*/
.serverImg{position: fixed; left: 1rem; bottom: 1.6rem; width: 13rem;}
.serverImg img{width: 100%;}

/*头部样式*/
.homeHeader{width: 70%;height: 6rem;background: url("./../assets/images/game2.png") no-repeat;background-size: 100% 100%;float: left;}
.headerLeft{width: 2.8rem; height: 2.8rem; text-align: center; float: left;margin-top:1.5rem; margin-left: 6%; background: url("./../assets/images/game3.png") no-repeat;background-size: 100% 100%;}
.userImg{width: 100%; height: 100%;border-radius: 6px;border: 1.5px solid #fff;}
.headerCenter{float: left;height: 4.5rem;margin-left: 0.6rem;margin-top: 1.4rem; font-size: 14px;}
.userTitle{margin: 0.4rem 0;color: #fff;font-size: 0.8rem;}
.userTitle span{padding-left: 0.2rem;}
.userMoney{width: 8rem; color: #fff; text-indent: 1.8rem; font-size: 0.8rem; line-height: 1.2rem; height: 1.2rem;background: url("./../assets/images/game5.png") no-repeat;background-size: 100% 100%;}
</style>



