<template>
  <div id="userTab">
    <mt-tabbar v-model="message" fixed  :style="{backgroundImage:'url('+bg+')'}">

      <mt-tab-item id="record">
        <span class="ico_sy ico_com"></span>
      </mt-tab-item>

      <mt-tab-item id="list">
        <span class="ico_wf ico_com"></span>
      </mt-tab-item>

      <mt-tab-item id="home">
        <span class="ico_tz ico_com"></span>
      </mt-tab-item>

      <mt-tab-item id="service">
        <span class="ico_my ico_com"></span>
      </mt-tab-item>

      <mt-tab-item id="userData">
        <span class="ico_data ico_com"></span>
      </mt-tab-item>

    </mt-tabbar>
  </div>
</template>

<script>

export default {
  name: 'userTab',
  data () {
    return {
      // 选中的tabbar值message为外面页面传入的值selected
      message: this.selected,
      // 这里使用的icon图标为图片，所以需要加图片改变的传入，若使用阿里图标，则不用加
      atabs: this.tabs,
      bg: require('./../assets/images/game19.png') // 底部导航背景
    }
  },
  props: {
    selected: String,
    tabs: Array
  },

  watch: {
    message: function (val, oldVal) {
      // 这里就可以通过 val 的值变更来确定去向
      switch (val) {
        case 'record':
          this.$router.push('/record')
          break
        case 'list':
          this.$router.push('/list')
          break
        case 'home':
          this.$router.push('/')
          break
        case 'service':
          this.$router.push('/service')
          break
        case 'userData':
          this.$router.push('/userData')
          break
      }
    }

  }

}

</script>
<style>
  .ico_com{display:block;  margin:0 auto;  }
  .ico_sy{width: 3.2rem; height: 3.5rem; margin-top: -1.5rem; background:url('./../assets/images/game73.png')no-repeat; background-size: 100%;}
  .ico_wf{width: 3.2rem; height: 3.5rem; margin-top: -1.5rem; background:url('./../assets/images/game11.png')no-repeat; background-size: 100%;}
  .ico_tz{width: 3.8rem; height: 3.5rem; margin-top: -1.5rem; background:url('./../assets/images/game14.png')no-repeat; background-size: 100%;}
  .ico_my{width: 3.2rem; height: 3.5rem; margin-top: -1.5rem; background:url('./../assets/images/game74.png')no-repeat; background-size: 100%;}
  .ico_data{width: 3.2rem; height: 3.5rem; margin-top: -1.5rem; background:url('./../assets/images/game75.png')no-repeat; background-size: 100%;}
  .mint-tabbar > .mint-tab-item.is-selected {background:none;color: #ff414f;cursor: pointer;}
  .mint-tabbar > .mint-tab-item.is-selected .ico_sy{background:url('./../assets/images/game77.png')no-repeat;background-size: 100%;}
  .mint-tabbar > .mint-tab-item.is-selected .ico_wf{background:url('./../assets/images/game12.png')no-repeat;background-size: 100%;}
  .mint-tabbar > .mint-tab-item.is-selected .ico_tz{background:url('./../assets/images/game13.png')no-repeat;background-size: 100%;}
  .mint-tabbar > .mint-tab-item.is-selected .ico_my{background:url('./../assets/images/game76.png')no-repeat;background-size: 100%;}
  .mint-tabbar > .mint-tab-item.is-selected .ico_data{background:url('./../assets/images/game78.png')no-repeat;background-size: 100%;}

</style>



