import Vue from 'vue'
import Router from 'vue-router'
import home from '@/page/home' // 首页页面
Vue.use(Router)

export default new Router({
  routes: [
    {
      path: '/wallet',
      name: 'wallet',
      component: () => import('@/page/wallet')
    }, // 钱包页面
    {
      path: '/record',
      name: 'record',
      component: () => import('@/page/record'),
      meta: {
        title: '北京赛车-走势',
        requireAuth: true
      }
    }, // 走势页面
    {
      path: '/',
      name: 'home',
      component: home,
      meta: {
        title: '首页',
        requireAuth: true
      }
    }, // 首页页面
    {
      path: '/service',
      name: 'service',
      component: () => import('@/page/service'),
      meta: {
        title: '北京赛车-代理',
        requireAuth: true
      }
    }, // 代理页面
    {
      path: '/userData',
      name: 'userData',
      component: () => import('@/page/userData'),
      meta: {
        title: '北京赛车-资料',
        requireAuth: true
      }
    }, // 资料页面
    {
      path: '/planningArea',
      name: 'planningArea',
      component: () => import('@/page/planningArea'),
      meta: {
        title: '北京赛车-计划专区',
        requireAuth: true
      }
    }, // 计划专区页面
    {
      path: '/list',
      name: 'list',
      component: () => import('@/page/list'),
      meta: {
        title: '北京赛车-记录',
        requireAuth: true
      }
    }, // 记录页面
    {
      path: '/games',
      name: 'games',
      component: () => import('@/page/games')
    }, // 游戏专区页面
    {
      path: '/client',
      name: 'client',
      component: () => import('@/page/client')
    }, // 客服页面
    {
      path: '/myData',
      name: 'myData',
      component: () => import('@/page/myData')
    }, // 资料页面
    {
      path: '/myProfit',
      name: 'myProfit',
      component: () => import('@/page/myProfit')
    }, // 我的收益页面
    {
      path: '/teamRecord',
      name: 'teamRecord',
      component: () => import('@/page/teamRecord')
    }, // 团队记录
    {
      path: '/myTeam',
      name: 'myTeam',
      component: () => import('@/page/myTeam')
    }, // 我的团队
    {
      path: '/register',
      name: 'register',
      component: () => import('@/page/register')
    }, // 注册页面
    {
      path: '/login',
      name: 'login',
      component: () => import('@/page/login')
    }, // 登录页面
    {
      path: '/feiting',
      name: 'feiting',
      component: () => import('@/page/feiting')
    } // 幸运飞艇记录页面
  ]
  // mode: 'history' // 干掉地址栏里边的#号键
})



