<template>
  <section id="user">
    <div class="homeHeader">
      <div class="headerLeft">
        <img :src="QR" alt="" class="userImg">
      </div>
      <div class="headerCenter">
        <div class="userTitle"><span>{{man.userName}}</span><span>{{man.id}}</span></div>
        <div class="userMoney">{{man.money}}</div>
      </div>
    </div>
  </section>
</template>

<script>
export default {
  name: 'user',
  data () {
    return {
      QR: require('../assets/images/game4.png'), // 用户头像
      man: {
        money: null, // 用户金额
        id: '', // 用户id
        userName: '' // 用户名
      },
      id: ''
    }
  },
  created () {
    var that = this
    let name = localStorage.getItem('name')
    this.$axios.post('http://www.xv88.cn/Lottery/Controllers/DataDealing.ashx?action=mes', {
      account: name
    }).then((data) => {
      // console.log(data)
      that.man.money = data.data[0].remainder
      that.man.id = data.data[0].show_id
      that.man.userName = data.data[0].show_name
      that.id = data.data[0].id
      // 把id存缓存
      localStorage.setItem('id', that.id)
      that.$store.commit('changeuserinfo', data.data.data)
    })
  }
}
</script>

<style scoped>
   #user{display: inline-block; width: 100%;}
  /*头部样式*/
  .homeHeader{width: 70%;height: 6rem;background: url("./../assets/images/game2.png") no-repeat;background-size: 100% 100%;float: left;}
  .headerLeft{width: 2.8rem; height: 2.8rem; text-align: center; float: left;margin-top:1.5rem; margin-left: 6%; background: url("./../assets/images/game3.png") no-repeat;background-size: 100% 100%;}
  .userImg{width: 100%; height: 100%;border-radius: 6px;border: 1.5px solid #fff;}
  .headerCenter{float: left;height: 4.5rem;margin-left: 0.6rem;margin-top: 1.4rem; font-size: 14px;}
  .userTitle{margin: 0.4rem 0;color: #fff;font-size: 0.8rem;}
  .userTitle span{padding-left: 0.2rem;}
  .userMoney{width: 8rem; color: #fff; text-indent: 1.8rem; font-size: 0.8rem; line-height: 1.2rem; height: 1.2rem;background: url("./../assets/images/game5.png") no-repeat;background-size: 100% 100%;}
</style>



